import React, { useState } from 'react';
import Accordion from 'react-bootstrap/Accordion';
import './faq.css';

import Button from 'react-bootstrap/Button';

function FAQSection() {
  const [activeItem, setActiveItem] = useState('');

  const handleAccordionToggle = (itemKey) => {
    setActiveItem(activeItem === itemKey ? '' : itemKey);
  };

  return (
    <div className="container-fluid a text-light">
      <h1 className="text-light mt-5 mb-5 head text-center">Frequently asked questions</h1>
      <Accordion  className="h custom-accordion">
        <Accordion.Item eventKey="1" className="a">
          <Accordion.Header onClick={() => handleAccordionToggle('1')}>
            <h3>What is Netflix?</h3>
          </Accordion.Header>
          <Accordion.Body className={`a ${activeItem === '1' ? 'show' : ''}`}>
            Netflix is a streaming service that lets you enjoy a wide variety of award-winning TV shows, movies, anime, and documentaries on thousands of Internet-connected devices. You can watch as much as you want, whenever you want, with no commercials  all for one low monthly fee. New content is added every week, so there's always something new to discover!
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="2" className="a">
          <Accordion.Header onClick={() => handleAccordionToggle('2')}>
            <h3>How much does Netflix cost?</h3>
          </Accordion.Header>
          <Accordion.Body className={`a ${activeItem === '2' ? 'show' : ''}`}>
            Netflix offers different plans at varying monthly costs. The pricing ranges from 149 to 649 per month, depending on the plan you choose. With your subscription, you can enjoy unlimited streaming of all the available content without any additional charges or contracts.
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="3" className="a">
          <Accordion.Header onClick={() => handleAccordionToggle('3')}>
            <h3>Where can I watch Netflix?</h3>
          </Accordion.Header>
          <Accordion.Body className={`a ${activeItem === '3' ? 'show' : ''}`}>
            Netflix can be accessed on various devices, including smartphones, tablets, smart TVs, laptops, and streaming devices. Simply download the Netflix app on your preferred device, sign in to your account, and start streaming. You can also access Netflix through a web browser by visiting netflix.com. Enjoy your favorite shows and movies anytime, anywhere!
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="4" className="a">
          <Accordion.Header onClick={() => handleAccordionToggle('3')}>
            <h3>How do I cancel?</h3>
          </Accordion.Header>
          <Accordion.Body className={`a ${activeItem === '3' ? 'show' : ''}`}>
          Netflix is flexible. There are no annoying contracts and no commitments. You can easily cancel your account online in two clicks. There are no cancellation fees  start or stop your account anytime.
                  </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="5" className="a">
          <Accordion.Header onClick={() => handleAccordionToggle('3')}>
            <h3>What can I watch on Netflix?</h3>
          </Accordion.Header>
          <Accordion.Body className={`a ${activeItem === '3' ? 'show' : ''}`}>
          Netflix has an extensive library of feature films, documentaries, TV shows, anime, award-winning Netflix originals, and more. Watch as much as you want, anytime you want.
             </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="6" className="a">
          <Accordion.Header onClick={() => handleAccordionToggle('3')}>
            <h3>Is Netflix good for Kids?</h3>
          </Accordion.Header>
          <Accordion.Body className={`a ${activeItem === '3' ? 'show' : ''}`}>
          The Netflix Kids experience is included in your membership to give parents control while kids enjoy family-friendly TV shows and films in their own space.

Kids profiles come with PIN-protected parental controls that let you restrict the maturity rating of content kids can watch and block specific titles you dont want kids to see.
             </Accordion.Body>
        </Accordion.Item>
      </Accordion>
      
      <p className="email-form-title text-center mt-5">Ready to watch? Enter your email to create or restart your membership.</p>
        <div className="input-group center">
          <input type="text" className="form-control bg-dark text-white col-6 c" placeholder="Email Address" />
          <Button variant="danger" className='ml-4'>Get Started{" >"}</Button>
          </div>
    <hr />
    </div>
  );
}

export default FAQSection;
