import './tv.css'; // Import your CSS 




import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';

function Tv(props) {
  const { title, description, image } = props;

  return (
    <Container className='con'>
    
      <Row className='row'>
        <Col sm={6}>
          <div className="our-story">
            <h1>{title}</h1>
            <h5>{description}</h5>
          </div>
        </Col>
        <Col sm={6}>
          <div className="tv-image">
            <img src={props.image} alt="TV" /> 
          </div>
          
        </Col>
      </Row>
      <br />
      
    </Container>
    
  );
}

export default Tv;
