import './login.css'; // Import your CSS 
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Offcanvas from 'react-bootstrap/Offcanvas';

import Button from 'react-bootstrap/Button';
import logo from './logo.png';
import { useState } from 'react';
import {useNavigate} from 'react-router-dom';



//Firebase
import { FirebaseConfig } from './fireconfig';
import { initializeApp } from "firebase/app";
import { getAuth ,signInWithEmailAndPassword } from "firebase/auth";


const LogIn = () => {
  //Login With Firebase
  const app = initializeApp(FirebaseConfig);
  const auth = getAuth(app);
  const [email,setEmail]=useState('');
  const [password,setPassword]=useState('');

  const emailOnChangeHandler=(e)=>{
 setEmail(e.target.value);



  }

 const passwordlOnChangeHandler=(e)=>{
  setPassword(e.target.value);

 }

  const navigate=useNavigate();

  const onSignInClickHandler=(e)=>{
    e.preventDefault();

    signInWithEmailAndPassword(auth, email, password)
    .then((userCredential) => {
      // Signed in 
      const user = userCredential.user;
      navigate('/dashboard');
      // ...
    })
    .catch((error) => {
      const errorCode = error.code;
      const errorMessage = error.message;
      alert(errorMessage);
    });
   



    //navigate('/dashboard');
  
  
  }

  const backToHome=(e)=>{
    e.preventDefault();
    navigate('/');
  
  }
  return(


<div>

<Navbar bg="transparent" expand='sm' className="mb-3 topNav topnav2">
    <Container fluid>
      <Navbar.Brand href="#" className="mr-4">

      <img
     src={logo}
           width="150"
        height="50"
           className="d-inline-block align-top logo logo2"
           alt="React Bootstrap logo"
           onClick={backToHome}
         />
      </Navbar.Brand>
     </Container>
  </Navbar>


    <div className='root'>
        {/* <div className="shadow"></div>
      <img className="concord-img vlv-creative" src="https://assets.nflxext.com/ffe/siteui/vlv3/6e32b96a-d4be-4e44-a19b-1bd2d2279b51/ee068656-14b9-4821-89b4-53b4937d9f1c/IN-en-20220516-popsignuptwoweeks-perspective_alpha_website_small.jpg"  alt=""></img> */}
     <div className='help'></div>
      <div id="feedback-form">
  <h2 class="header"> Sign In</h2>
  <div>
    <form>
      <input type="text" name="email" value={email} onChange={emailOnChangeHandler} placeholder="Email or phone Number" className=' bg-dark text-white'></input>
      <input type="password" name="password" value={password} placeholder="Password" onChange={passwordlOnChangeHandler} className=' bg-dark text-white mt-4'></input>
      <br></br>
      <input type="checkbox" id="feedback-notify" name="notify" />
     
      <label for="feedback-notify">Remember Me</label>
     <br />
     <br />
     <br />
      <Button variant="danger" className='but' onClick={onSignInClickHandler}>Sign In</Button>
      <br />
      <br />
      <br />
      <div className='exit-text'>New to Netflix? Sign up now.<br />
This page is protected by Google reCAPTCHA to ensure you're not a bot.
<a href=""> Learn more.</a></div>
    </form>
  </div>
</div>    
    </div>
    </div>


  )
}

export default LogIn;