import Card from 'react-bootstrap/Card';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Offcanvas from 'react-bootstrap/Offcanvas';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import logo from './logo.png';
import Button from 'react-bootstrap/Button';


import { useState,React } from 'react';
import {useNavigate} from 'react-router-dom';
import './finalsignup.css';


import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";
import { FirebaseConfig } from './fireconfig';
import { initializeApp } from "firebase/app";








function FinalSignUp() {
    const navigate=useNavigate();


    const app = initializeApp(FirebaseConfig);
    const auth = getAuth(app);
    const [email,setEmail]=useState('');
    const [password,setPassword]=useState('');
  
    const emailOnChangeHandler=(e)=>{
   setEmail(e.target.value);
  
  
  
    }
  
   const passwordlOnChangeHandler=(e)=>{
    setPassword(e.target.value);
  
   }
  

   const onSignInClickHandler=(e)=>{
    e.preventDefault();

    createUserWithEmailAndPassword(auth, email, password)
  .then((userCredential) => {
    // Signed in 
    const user = userCredential.user;
    alert("Welcome to amazing world of Netflix , enjoy the binge ");
    navigate('/dashboard');
    // ...
  })
  .catch((error) => {
    const errorCode = error.code;
    const errorMessage = error.message;
    alert(errorMessage);
    // ..
  });


    //navigate('/dashboard');
  
  
  }





  const backToHome=(e)=>{
    e.preventDefault();
    navigate('/');
  }

  const secondSign=(e)=>{
    e.preventDefault();
    navigate('/secondSign');
  }
  return (
    <div>


<img
     src={logo}
           width="150"
        height="50"
           className="d-inline-block align-top signlogo "
           alt="React Bootstrap logo"
           onClick={backToHome}
         />
  <div className='plans'>
         <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-check2-circle" viewBox="0 0 16 16">
  <path d="M2.5 8a5.5 5.5 0 0 1 8.25-4.764.5.5 0 0 0 .5-.866A6.5 6.5 0 1 0 14.5 8a.5.5 0 0 0-1 0 5.5 5.5 0 1 1-11 0z"/>
  <path d="M15.354 3.354a.5.5 0 0 0-.708-.708L8 9.293 5.354 6.646a.5.5 0 1 0-.708.708l3 3a.5.5 0 0 0 .708 0l7-7z"/>
</svg>  {    }<br/> Step 3 of 3
            <h2 className='sign2'> Enter an email and password to create an account

            <h5>Just a few more steps and you're done!
We hate paperwork, too.</h5>
</h2>

         </div>


<div className='login-form'>

         <Form>
      <Form.Group className="mb-3" controlId="formBasicEmail">
        <Form.Label>Email address</Form.Label>
        <Form.Control type="email" placeholder="Enter email" value={email} onChange={emailOnChangeHandler} />
        <Form.Text className="text-muted">
          We'll never share your email with anyone else.
        </Form.Text>
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label>Password</Form.Label>
        <Form.Control type="password" placeholder=" Add a password"  value={password} onChange={passwordlOnChangeHandler}/>
      </Form.Group>
      <Form.Group className="mb-3" controlId="formBasicCheckbox">
        
      </Form.Group>
      <Button className='but'onClick={onSignInClickHandler} type="submit" >
        Submit and Pay
      </Button>
    </Form>

</div>










      </div>
  );
}

export default FinalSignUp;