import './Navbar.css'; // Import your CSS 

import {useNavigate} from 'react-router-dom';


import Button from 'react-bootstrap/Button';
const HomeBanner = () => {
  
  const navigate=useNavigate();
  const sign=(e)=>{
    e.preventDefault();
    navigate('/signup');
  }
  return(
    <div className="home-banner">
      <div className="our-story">
        <h1 className="our-story-card-title" data-uia="hero-title">Unlimited movies, TV shows and more.</h1>
        <h2 className="our-story-card-subtitle" data-uia="our-story-card-subtitle">Watch anywhere. Cancel anytime.</h2>
        <p className="email-form-title">Ready to watch? Enter your email to create or restart your membership.</p>
        <div className="input-group">
          <input type="text" className="form-control bg-dark text-white c col-6" placeholder="Email Address" />
          <Button variant="danger" onClick={sign}>Get Started {" >"}</Button>
          </div>
      </div>
      <div className="shadow"></div>
      
      <img className="concord-img vlv-creative" src="https://assets.nflxext.com/ffe/siteui/vlv3/51e53f54-0d9f-40ec-9e05-c030def06ac9/be5b2939-da9f-4b9f-b76b-31b32ad0515b/IN-en-20230515-popsignuptwoweeks-perspective_alpha_website_small.jpg"  alt=""></img>
    </div>
  )
}

export default HomeBanner;